import { Controller } from '@ali/4ever-cangjie';
import { SpacingProperties } from '@ali/4ever-mo';


export default function getLineHeight(controller: Controller) {
  const commonSpacing = controller.query('getCommonSpacing') as SpacingProperties;

  return {
    line: commonSpacing.line,
    lineRule: commonSpacing.lineRule,
  };
}
